.navbar {
  display: flex;
  width: 100%;
  justify-content: center;
}
.navbar ul {
  display: flex;
  gap: 20px;
  list-style: none;
}
.navbar ul .chosen {
  background: #EFF1F1;
  border-radius: 8px;
}
.navbar ul .chosen a {
  color: #302B62;
}
.navbar ul li {
  padding: 8px 12px;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
}
.navbar ul li:hover {
  background: #EFF1F1;
  border-radius: 8px;
}
.navbar ul li a {
  text-decoration: none;
  color: #5B5B5B;
}

