.preloader {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.preloader__circle {
  display: inline-block;
  width: 60px;
  height: 60px;
}
.preloader__circle:after {
  content: " ";
  display: block;
  width: 45px;
  height: 45px;
  margin: 8px;
  border-radius: 50%;
  border: 6px solid #27ae60;
  border-color: #27ae60 transparent #27ae60 transparent;
  animation: spinner 1.5s linear infinite;
}

@keyframes spinner {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

